import React from "react";
import { PanelBody } from '@wordpress/components';
import {__} from '@wordpress/i18n';
import AabColorPicker from "../../../components/aab-colorpicker";

export default function CategoryFilterStylesPanel({ attributes, setAttributes }) {
  const {
    filterBtnColor,
    filterBtnBg,
    filterBtnHoverColor,
    filterBtnHoverBg,
    filterBtnActiveColor,
    filterBtnActiveBg,
    enableCategoryFilter,
  } = attributes;

  if(!enableCategoryFilter) return <></>;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Category Filter', 'advanced-accordion-block')}
    >
      <AabColorPicker
        label={__('Default Color', 'advanced-accordion-block')}
        value={filterBtnColor}
        onChange={(filterBtnColor) => setAttributes({filterBtnColor})}
        bgValue={filterBtnBg}
        onBgChange={(filterBtnBg) => setAttributes({filterBtnBg})}
      />
      <AabColorPicker
        label={__('Hover Color', 'advanced-accordion-block')}
        value={filterBtnHoverColor}
        onChange={(filterBtnHoverColor) => setAttributes({filterBtnHoverColor})}
        bgValue={filterBtnHoverBg}
        onBgChange={(filterBtnHoverBg) => setAttributes({filterBtnHoverBg})}
      />
      <AabColorPicker
        label={__('Active Color', 'advanced-accordion-block')}
        value={filterBtnActiveColor}
        onChange={(filterBtnActiveColor) => setAttributes({filterBtnActiveColor})}
        bgValue={filterBtnActiveBg}
        onBgChange={(filterBtnActiveBg) => setAttributes({filterBtnActiveBg})}
      />
    </PanelBody>
  );
}