import { registerBlockType } from '@wordpress/blocks';
import metadata from './block.json';

/**
 * Internal dependencies
 */
import Edit from './edit';
import Save from './save';
import saveDep from './deprecated';

import GroupAccordionBlockIcon from '../components/icons/block-icons/group-accordion';

/**
 * Block Registration
 */
registerBlockType(metadata, {
	icon: {
		src: GroupAccordionBlockIcon,
		foreground: '#77b5f7',
	},
	edit: Edit,
	save: Save,
	deprecated: [
		{
			"attributes": {
				"anchorLinksShow": {
					"type": "boolean",
					"default": false
				},
				"QaStyle": {
					"type": "boolean",
					"default": false
				},
				"uniqueId": {
					"type": "string"
				},
				"activeAccordionBorder": {
					"type": "object",
					"default": {
						"width": "1px",
						"color": "#ebebeb",
						"style": "solid"
					}
				},
				"searchShow": {
					"type": "boolean",
					"default": false
				},
				"placeholderText": {
					"type": "string"
				},
				"showAllbtn": {
					"type": "boolean",
					"default": false
				},
				"closeText": {
					"type": "string",
					"default": "Close all"
				},
				"openText": {
					"type": "string",
					"default": "Show all"
				},
				"heading": {
					"type": "string",
					"default": "Accordion Heading"
				},
				"subheading": {
					"type": "string",
					"default": ""
				},
				"subheadingColor": {
					"type": "string"
				},
				"activetorClass": {
					"type": "string",
					"default": "click"
				},
				"faqSchema": {
					"type": "boolean",
					"default": false
				},
				"step": {
					"type": "boolean",
					"default": false
				},
				"stepText": {
					"type": "string",
					"default": "Continue"
				},
				"stepCmpltText": {
					"type": "string",
					"default": "Congratulations, you are done!"
				},
				"checkList": {
					"type": "boolean",
					"default": false
				},
				"buttonShow": {
					"type": "boolean",
					"default": false
				},
				"contentCount": {
					"type": "number",
					"default": 300
				},
				"readText": {
					"type": "string",
					"default": "Read More"
				},
				"border": {
					"type": "object",
					"default": {
						"width": "1px",
						"color": "#bcb6b638",
						"style": "solid"
					}
				},
				"paddings": {
					"type": "object",
					"default": {
						"top": "",
						"right": "",
						"left": "",
						"bottom": ""
					}
				},
				"margins": {
					"type": "object",
					"default": {
						"top": "0px",
						"right": "",
						"left": "",
						"bottom": "15px"
					}
				},
				"borderRadius": {
					"type": "number"
				},
				"headingColor": {
					"type": "string"
				},
				"headerBg": {
					"type": "string",
					"default": "#bcb6b638"
				},
				"iconColor": {
					"type": "string"
				},
				"iconBackground": {
					"type": "string"
				},
				"bodyBg": {
					"type": "string"
				},
				"showIcon": {
					"type": "boolean",
					"default": true
				},
				"qIconText": {
					"type": "string",
					"default": "Q"
				},
				"qIconColor": {
					"type": "string",
					"default": "#fff"
				},
				"qIconBg": {
					"type": "string",
					"default": "#505050"
				},
				"aIconText": {
					"type": "string",
					"default": "A"
				},
				"aIconColor": {
					"type": "string",
					"default": "#fff"
				},
				"aIconBg": {
					"type": "string",
					"default": "#f5a623"
				},
				"customCSS": {
					"type": "string",
					"default": ""
				},
				"openALLBtnPosition": {
					"type": "string",
					"default": "right_btn"
				},
				"headingBorder": {
					"type": "object",
					"default": {
						"color": null,
						"style": "solid",
						"width": "0px"
					}
				},
				"bodyBorder": {
					"type": "object",
					"default": {
						"top": {
							"color": "#bcb6b638",
							"style": "solid",
							"width": "1px"
						}
					}
				},
				"activeBackground": {
					"type": "string"
				},
				"categoryList": {
					"type": "array"
				},
				"filterBtnAlignment": {
					"type": "string",
					"default": "center_btn"
				},
				"filterBtnPosition": {
					"type": "string",
					"default": "horizontal_filter"
				},
				"filterBtnColor": {
					"type": "string",
					"default": "#0866ff"
				},
				"filterBtnBg": {
					"type": "string",
					"default": "#10b3d608"
				},
				"filterBtnActiveColor": {
					"type": "string",
					"default": "#fff"
				},
				"filterBtnActiveBg": {
					"type": "string",
					"default": "#0866ff"
				},
				"enableCategoryFilter": {
					"type": "boolean",
					"default": false
				},
				"showMoreBtn": {
					"type": "boolean",
					"default": false
				},
				"showMoreBtnTxt": {
					"type": "string",
					"default": "Show More"
				},
				"showLessBtnTxt": {
					"type": "string",
					"default": "Show Less"
				},
				"itemsToShow": {
					"type": "number",
					"default": 5
				},
				"showMoreBtnColor": {
					"type": "string",
					"default": "#010101"
				},
				"showMoreBtnBg": {
					"type": "string",
					"default": "#eaeaea"
				},
				"labelsGlobalTextColor": {
					"type": "string",
					"default": "#1570EC"
				},
				"labelsGlobalBgColor": {
					"type": "string",
					"default": "#c1d8f7"
				}
			},
			migrate: (attributes) => {
				if(attributes.accessibilityOn === undefined) attributes.accessibilityOn = true;
				if(attributes.focusOutlineColor === undefined) attributes.focusOutlineColor = "#C2DBFE";

				if(attributes.filterBtnBg === "#10b3d608") attributes.filterBtnBg = "#F4F8FF";
				if(attributes.filterBtnActiveColor === "#fff") attributes.filterBtnActiveColor = "#F4F8FF";

				if(attributes.filterBtnHoverColor === undefined) attributes.filterBtnHoverColor = "#0866ff";
				if(attributes.filterBtnHoverBg === undefined) attributes.filterBtnHoverBg = "#DDE9FD";

				return { ...attributes };
			},
			save: saveDep,
		},
	]
});
