import React from 'react';
import { InspectorControls } from '@wordpress/block-editor';
import {__} from '@wordpress/i18n';

import '../editor.scss';
import QaIconsStylesPanel from './styles/qa-icons';
import ActiveAccordionStylesPanel from './styles/active-accordion';
import AccordionStylesPanel from './styles/accordion';
import AccordionHeadStylesPanel from './styles/accordion-head';
import AccordionIconStylesPanel from './styles/accordion-icon';
import AccordionBodyStylesPanel from './styles/accordion-body';
import CustomCSSPanel from './styles/custom-css';
import AccordionIdPanel from './settings/accordion-id';
import AccordionSearchPanel from './settings/accordion-search';
import ShowAllBtnPanel from './settings/show-all-btn';
import StepsChecklistPanel from './settings/steps-checklist-panel';
import ReadMoreBtnPanel from './settings/read-more-btn';
import CategoryFilterPanel from './settings/category-filter';
import ShowMoreBtnPanel from './settings/show-more-btn';
import MoreFeaturesPanel from './settings/more-features';
import CategoryFilterStylesPanel from './styles/category-filter';
import AccessibilityPanel from './settings/accessibility';

function Settings(props) {
  return (
    <InspectorControls>
      <AccordionIdPanel {...props} />
      <AccordionSearchPanel {...props} />
      <ShowAllBtnPanel {...props} />
      <StepsChecklistPanel {...props} />
      <ReadMoreBtnPanel {...props} />
      <CategoryFilterPanel {...props} />
      <ShowMoreBtnPanel {...props} />
      <MoreFeaturesPanel {...props} />
      <AccessibilityPanel {...props} />
    </InspectorControls>
  );
}

function Styles(props) {
  return (
    <>
      <InspectorControls group="styles">
        <QaIconsStylesPanel {...props} />
        <ActiveAccordionStylesPanel {...props} />
        <AccordionStylesPanel {...props} />
        <AccordionHeadStylesPanel {...props} />
        <AccordionIconStylesPanel {...props} />
        <AccordionBodyStylesPanel {...props} />
        <CategoryFilterStylesPanel {...props} />
        <CustomCSSPanel {...props} />
      </InspectorControls>
    </>
  );
}

const Editor = { Settings, Styles };
export default Editor;