import React from "react";
import { PanelBody, ToggleControl, FormTokenField, SelectControl } from "@wordpress/components";
import {__} from '@wordpress/i18n';

const filterBtnPositions = [
  {
    label: 'Vertical',
    value: 'vertical_filter',
  },
  {
    label: 'Horizontal',
    value: 'horizontal_filter',
  },
];

const filterBtnAlignments = [
  {
    label: 'Left',
    value: 'left_btn',
  },
  {
    label: 'Center',
    value: 'center_btn',
  },
  {
    label: 'Right',
    value: 'right_btn',
  }
];

export default function CategoryFilterPanel({ attributes, setAttributes }) {
  const { enableCategoryFilter, categoryList, filterBtnPosition, filterBtnAlignment } = attributes;
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';

  return (
    <PanelBody
      initialOpen={false}
      title={__('Category Filter', 'advanced-accordion-block')}
      className={has_disabled_class}
    >
      <ToggleControl
        label={__('Enable Category Filter', 'advanced-accordion-block')}
        checked={enableCategoryFilter}
        disabled={!aab_premium}
        onChange={(enableCategoryFilter) => setAttributes({ enableCategoryFilter })}
      />
      {enableCategoryFilter && (
        <>
          <FormTokenField
            __experimentalAutoSelectFirstMatch
            __experimentalExpandOnFocus
            __next40pxDefaultSize
            __nextHasNoMarginBottom
            label={__(
              'Add Category Tags',
              'advanced-accordion-block'
            )}
            onChange={(categoryList) => setAttributes({ categoryList })}
            suggestions={[]}
            value={categoryList}
            disabled={!aab_premium}
          />

          <SelectControl
            label={__(
              'Filter Button Position',
              'advanced-accordion-block'
            )}
            disabled={!aab_premium}
            options={filterBtnPositions}
            onChange={(filterBtnPosition) => setAttributes({filterBtnPosition})}
            value={filterBtnPosition}
          />

          {filterBtnPosition === 'horizontal_filter' && (
            <SelectControl
              label={__(
                'Filter Button Alignment',
                'advanced-accordion-block'
              )}
              disabled={!aab_premium}
              options={filterBtnAlignments}
              onChange={(filterBtnAlignment) => setAttributes({filterBtnAlignment})}
              value={filterBtnAlignment}
            />
          )}
        </>
      )}
    </PanelBody>
  )
}