import React from "react";
import {
  __experimentalToggleGroupControl as ToggleGroupControl,
  __experimentalToggleGroupControlOption as ToggleGroupControlOption,
  PanelBody,
  ToggleControl
} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import {createAttrGetter} from "../../../utils/global-settings";

export default function MoreFeaturesPanel({ attributes, setAttributes }) {
  const { step, activetorClass } = attributes;
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';
  const anchorLinksShow =
      attributes.anchorLinksShow !== false
          ? attributes.anchorLinksShow === true
              ? true
              : attributes.defaultStyles?.anchorLinkShow === true
          : false;

  const getAttrDeep = createAttrGetter(attributes);
  const faqSchema = getAttrDeep('faqSchema') ?? false;
  return (
    <PanelBody
      title={__('More Features', 'advanced-accordion-block')}
      initialOpen={false}
      className={has_disabled_class}
    >
      {!step && (
        <ToggleControl
          label={__(
            'Anchor Link',
            'advanced-accordion-block'
          )}
          disabled={!aab_premium}
          checked={aab_premium && anchorLinksShow}
          onChange={(anchorLinksShow) => setAttributes({ anchorLinksShow })}
        />
      )}
      <ToggleControl
        label={__(
          'FAQ Schema',
          'advanced-accordion-block'
        )}
        checked={faqSchema}
        onChange={(faqSchema) => setAttributes({ faqSchema })}
      />
      <div>
        {/* <div className='aab__label'>{__('Activator Event', 'advanced-accordion-block')}</div> */}

        <ToggleGroupControl
          value={activetorClass}
          onChange={activetorClass => setAttributes({ activetorClass })}
          label={__('Activator Event', 'advanced-accordion-block')}
        >
          <ToggleGroupControlOption
            value="click"
            label="Click"
            disabled={!aab_premium}
            showTooltip
          />
          <ToggleGroupControlOption value="hover" disabled={!aab_premium} label="Hover"/>
        </ToggleGroupControl>
      </div>
    </PanelBody>
  )
}