// Import necessary WordPress packages
import apiFetch from '@wordpress/api-fetch';


// Function to load global settings from the WordPress database
export const loadGlobalSettings = async () => {
    try {
        const data = await apiFetch({ path: '/wp/v2/settings' });
        const savedGlobalStyles = data.aab_settings_defaults
            ? JSON.parse(data.aab_settings_defaults)
            : {};
        return {...savedGlobalStyles };
    } catch (error) {
        console.error('Error loading global styles:', error);
        return  null; // Return default values in case of an error
    }
};



export const createAttrGetter = (attributes) => {
    return (path) => {
        const keys = path.split('.');
        let val = attributes;

        for (let key of keys) val = val?.[key];
        if (val !== undefined) return val;

        val = attributes.defaultStyles;
        for (let key of keys) val = val?.[key];

        return val;
    };
};