import {useState} from '@wordpress/element';
import {
    Button,
    ColorIndicator,
    Popover,
    ColorPicker,
    ColorPalette,
    Flex,
    FlexItem,
    BaseControl,
    TabPanel,
    __experimentalZStack as ZStack
} from '@wordpress/components';

import colors from "../colors";
import React from "react";
import './colorpicker.scss';

const AabColorPicker = ({value, onChange, bgValue, onBgChange, label, innerLabel = ['Text', 'Background']}) => {

    const [isVisible, setIsVisible] = useState(false);
    const [color, setColor] = useState(value);
    const [bgColor, setBgColor] = useState(bgValue);
    let tabListNames = () => {
        if (onBgChange) {
            return [
                {
                    name: 'color',
                    title: `${innerLabel[0] || 'Text'}`,
                    className: 'tab-color',
                },
                {
                    name: 'background',
                    title: `${innerLabel[1] || 'Background'}`,
                    className: 'tab-background',
                },
            ];
        } else {
            return [
                {
                    name: 'color',
                    title: `${innerLabel[0] || 'Text'}`,
                    className: 'tab-color',
                }
            ]
        }

    };

    const toggleVisible = () => {
        setIsVisible(!isVisible);
    };
    const handleColorChange = (newColor) => {
        setColor(newColor);
        if (onChange) {
            onChange(newColor); // Pass the color to the parent
        }
    };

    const handleBgColorChange = (newColor) => {
        setBgColor(newColor);
        if (onBgChange) {
            onBgChange(newColor); // Pass the color to the parent
        }
    };

    return (
        <div className='aab-color-picker' style={{'position': 'relative'}}>
            <Flex>
                <FlexItem>
                    <BaseControl
                        label={label}
                    ></BaseControl>
                </FlexItem>
                <FlexItem>
                    <Flex>
                        <FlexItem>
                            <Button
                                className={`aab-reset-btn ${color || bgColor ? 'active' : 'disabled'}`}
                                onClick={function () {
                                    handleColorChange('');
                                    handleBgColorChange('');
                                }}
                                icon='image-rotate'
                                showTooltip={true}
                                label='Reset'
                            >
                            </Button>
                        </FlexItem>
                        <FlexItem>
                            <Button onClick={toggleVisible}>
                                <ZStack offset={14}>
                                    {onChange && (<ColorIndicator colorValue={color}/>)}
                                    {onBgChange && (<ColorIndicator colorValue={bgColor}/>)}

                                </ZStack>
                            </Button>
                        </FlexItem>
                    </Flex>
                    {isVisible &&
                        <Popover placement="left" className='aab-color-popover'
                                 onFocusOutside={() => setIsVisible(false)}>

                            <TabPanel
                                className="my-tab-panel"
                                activeClass="active-tab"
                                tabs={tabListNames()}
                            >
                                {(tab) => <>

                                    {tab.name === 'color' && onChange && (
                                        <>
                                            <ColorPicker
                                                enableAlpha
                                                onChange={(newColor) => handleColorChange(newColor)}
                                                color={color}
                                            />
                                            <div style={{'padding': '0 14px 16px 14px'}}>
                                                <ColorPalette
                                                    colors={colors}
                                                    value={color}
                                                    asButtons={true}
                                                    disableCustomColors={true}
                                                    onChange={(newColor) => handleColorChange(newColor)}
                                                    clearable={false}
                                                />
                                            </div>
                                        </>
                                    )}
                                    {tab.name === 'background' && onBgChange && (
                                        <>
                                            <ColorPicker
                                                enableAlpha
                                                onChange={(newColor) => handleBgColorChange(newColor)}
                                                color={bgColor}
                                            />
                                            <div style={{'padding': '0 14px 16px 14px'}}>
                                                <ColorPalette
                                                    colors={colors}
                                                    value={bgColor}
                                                    asButtons={true}
                                                    disableCustomColors={true}
                                                    onChange={(newColor) => handleBgColorChange(newColor)}
                                                    clearable={false}
                                                />
                                            </div>
                                        </>
                                    )}

                                </>}
                            </TabPanel>
                        </Popover>}
                </FlexItem>
            </Flex>
        </div>
    );
};

export default AabColorPicker;