import React from "react";
import { PanelBody, TextControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

export default function AccordionIdPanel({ prefix = "aab_accordion", attributes, setAttributes }) {

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion ID', 'advanced-accordion-block')}
    >
      <TextControl
        label={__(
          'Set Accordion ID',
          'advanced-accordion-block'
        )}
        value={`${prefix}_${attributes.uniqueId}`}
        onChange={function (uniqueId) {
          if (uniqueId !== prefix) {
            const slicedWord = uniqueId.replace(`${prefix}_`, "");
            setAttributes({uniqueId: slicedWord});
          }
        }}
      />
    </PanelBody>
  );
}