import React from "react";
import {createAttrGetter} from "../../utils/global-settings";

function Styles(props) {
  const { 
    uniqueId,
    step,
    activeAccordionBorder,
    accessibilityOn,
    focusOutlineColor
  } = props.attributes;


  const getAttrDeep = createAttrGetter( props.attributes);
  const anchorLinksShow =
      props.attributes.anchorLinksShow !== false
          ? props.attributes.anchorLinksShow === true
              ? true
              : props.attributes.defaultStyles?.anchorLinkShow === true
          : false;
  const headingColor = getAttrDeep('headingColor') ?? {};
  return (
    <style>
      {`
        .aagb_accordion_${uniqueId} .aagb__accordion_active .aagb__accordion_body {
          border-top: ${activeAccordionBorder.width} ${activeAccordionBorder.style} ${activeAccordionBorder.color};
        }
      `}

      {anchorLinksShow && headingColor && !step && (
          `.aagb_accordion_${uniqueId} .aagb__accordion_heading .anchorjs-link {
            color: ${headingColor}
          }`
      )}
      {
        accessibilityOn ?
        `
          .aagb_accordion_${uniqueId} .aagb__accordion_container {
            transition-duration: 0ms !important;
            outline: 2px solid #00000000;
          }
          .aagb_accordion_${uniqueId} .aagb__accordion_container:focus-visible {
            outline: 2px solid ${focusOutlineColor};
          }
        ` : ``
      }
  </style>
  )
}

function Scripts(props) {
  const { uniqueId, step } = props.attributes;
  const anchorLinksShow =
      props.attributes.anchorLinksShow !== false
          ? props.attributes.anchorLinksShow === true
              ? true
              : props.attributes.defaultStyles?.anchorLinkShow === true
          : false;
  const aab_premium = aagb_local_object.licensing;

  if(!anchorLinksShow || !aab_premium || step) return <></>;

  return (
    <script>
      {`
        document.addEventListener("DOMContentLoaded", () => {
          const Anchor1 = new AnchorJS();
          Anchor1.add('.aagb_accordion_${uniqueId} .aagb__accordion_heading .title_wrapper');
        });     
      `}
    </script>
  );
}

const Other = { Styles, Scripts }
export default Other;