import React from "react";
import { PanelBody, __experimentalBorderBoxControl as BorderBoxControl, } from '@wordpress/components';
import {__} from '@wordpress/i18n';
import { useSelect } from "@wordpress/data";
import colors from "../../../colors";
import AabColorPicker from "../../../components/aab-colorpicker";

export default function AccordionHeadStylesPanel({ attributes, setAttributes, clientId }) {
  const {
    QaStyle,
    headerBg,
    headingColor,
    headingBorder,
    subheadingColor,
    labelsGlobalTextColor,
    labelsGlobalBgColor,
  } = attributes;
  const aab_premium = aagb_local_object.licensing;

  const hasLabels = useSelect((select) => {
    const blocks = select("core/block-editor")?.getBlock(clientId)?.innerBlocks || [];
    return blocks.some(block => block.attributes?.enableLabels); // at least one accordion-item has labels enabled.
  }, []);

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Head', 'advanced-accordion-block')}
    >
      <AabColorPicker
        label={__('Header Color', 'advanced-accordion-block')}
        value={headingColor}
        onChange={(headingColor) => setAttributes({headingColor})}
        bgValue={headerBg}
        onBgChange={(headerBg) => setAttributes({headerBg})}
      />

      {aab_premium && (
        <AabColorPicker
          label={__('Sub Heading Color', 'advanced-accordion-block')}
          value={subheadingColor}
          onChange={(subheadingColor) =>
            setAttributes({subheadingColor})
          }
          bgValue={undefined}
          onBgChange={undefined}
        />
      )}

      {
        hasLabels && (
          <>
            <div className='aa-custom-spacer'></div>
            <AabColorPicker
              label={__('Labels Color', 'advanced-accordion-block')}
              value={labelsGlobalTextColor}
              onChange={(labelsGlobalTextColor) => setAttributes({labelsGlobalTextColor})}
              bgValue={labelsGlobalBgColor}
              onBgChange={(labelsGlobalBgColor) => setAttributes({labelsGlobalBgColor})}
            />
          </>
        )
      }

      {!QaStyle && (
        <>
          <div className='aa-custom-spacer'></div>
          <BorderBoxControl
            colors={colors}
            label={__('Borders')}
            onChange={(headingBorder) => setAttributes({ headingBorder })}
            value={headingBorder}
          />
        </>
      )}
    </PanelBody>
  );
}