import React from "react";
import { RichText } from '@wordpress/block-editor';
import ExpandIcon from "../../components/icons/expand";
import MinimizeIcon from "../../components/icons/minimize";

export default function ShowAllBtn(props) {
  const { uniqueId, showAllbtn, closeText, openText, openALLBtnPosition, accessibilityOn } = props.attributes;
  const aab_premium = aagb_local_object.licensing;

  if(!aab_premium || !showAllbtn) return <></>;

  return (
    <div className={`aagb_accordion_wrapper_btn ${openALLBtnPosition}`}>
      <a tabIndex={accessibilityOn ? 0 : -1} href="#" data-openTarget={'aagb_accordion_' + uniqueId}
        className="content-accordion__show-all">
        <ExpandIcon />
        <RichText.Content
          value={openText}
          style={{
            margin: 0,
          }}
        />
      </a>
      <a tabIndex={accessibilityOn ? 0 : -1} href="#" data-closeTarget={'aagb_accordion_' + uniqueId}
        className="content-accordion__close-all">
        <MinimizeIcon />
        <RichText.Content
          value={closeText}
          style={{
            margin: 0,
          }}
        />
      </a>
  </div>
  )
}