import React from "react";
import { PanelBody, ToggleControl, RangeControl, TextControl } from "@wordpress/components";
import { useSelect } from '@wordpress/data';
import {__} from '@wordpress/i18n';
import AabColorPicker from "../../../components/aab-colorpicker";

export default function ShowMoreBtnPanel({ attributes, setAttributes, clientId }) {
  const { showMoreBtn, itemsToShow, showMoreBtnTxt, showLessBtnTxt, showMoreBtnColor, showMoreBtnBg } = attributes;
  const blocks = useSelect('core/block-editor', []).getBlock(clientId)?.innerBlocks;
  
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';

  return (
    <PanelBody
      initialOpen={false}
      title={__('Show More Button', 'advanced-accordion-block')}
      className={has_disabled_class}
    >
      <div
        style={{
          paddingTop: 5,
          paddingBottom: 5,
        }}
      >
        <ToggleControl
          label={__(
            'Enable Show More Button',
            'advanced-accordion-block'
          )}
          checked={showMoreBtn}
          disabled={!aab_premium}
          onChange={(showMoreBtn) => setAttributes({ showMoreBtn })}
        />
      </div>
      {showMoreBtn && (
        <div>
          <RangeControl
            label={__(
              'Items Per Click',
              'advanced-accordion-block'
            )}
            help={__(
              'How many items to show per click?',
              'advanced-accordion-block'
            )}
            value={itemsToShow}
            min={1}
            max={blocks?.length}
            disabled={
              !aab_premium ||
              !showMoreBtn ||
              blocks?.length < 2
            }
            onChange={(itemsToShow) => setAttributes({ itemsToShow })}
          />

          <TextControl
            label={__('Show More Text', 'advanced-accordion-block')}
            value={showMoreBtnTxt}
            onChange={(showMoreBtnTxt) => setAttributes({ showMoreBtnTxt })}
            help={__('Enter the text you want to display on the Show more button.', 'advanced-accordion-block')}
          />

          <TextControl
            label={__('Show Less Text', 'advanced-accordion-block')}
            value={showLessBtnTxt}
            onChange={(showLessBtnTxt) => setAttributes({ showLessBtnTxt })}
            help={__('Enter the text you want to display on the Show less button.', 'advanced-accordion-block')}
          />

          <AabColorPicker
            label={__('Button Color', 'advanced-accordion-block')}
            value={showMoreBtnColor}
            onChange={(showMoreBtnColor) => setAttributes({showMoreBtnColor})}
            bgValue={showMoreBtnBg}
            onBgChange={(showMoreBtnBg) => setAttributes({showMoreBtnBg})}
          />
        </div>
      )}
    </PanelBody>
  )
}