import React from "react";
import { PanelBody, ToggleControl } from '@wordpress/components';
import {__} from '@wordpress/i18n';
import AabColorPicker from "../../../components/aab-colorpicker";

export default function AccordionIconStylesPanel({ attributes, setAttributes }) {
  const { showIcon, iconColor, iconBackground } = attributes;

  return (
    <PanelBody
      title={__('Accordion Icon', 'advanced-accordion-block')}
      initialOpen={false}
    >
      <ToggleControl
        label={__('Show Icon', 'advanced-accordion-block')}
        checked={showIcon}
        onChange={(showIcon) => setAttributes({ showIcon })}
      />
      {showIcon && (
        <>
          <AabColorPicker
            label={__('Color', 'advanced-accordion-block')}
            value={iconColor}
            onChange={(iconColor) => setAttributes({iconColor})}
            bgValue={iconBackground}
            onBgChange={(iconBackground) => setAttributes({iconBackground})}
            innerLabel={['Icon', 'Background']}
          />
        </>
      )}
    </PanelBody>
  );
}