import React from "react";
import {InnerBlocks, useBlockProps as useBlockDefaultProps} from '@wordpress/block-editor';

import ShowMoreBtn from './show-more-btn';
import CategoryFilter from './category-filter';
import Search from './search';
import ShowAllBtn from './show-all-btn';
import CustomCSSBlock from './custom-css-block';
import Other from './other';

export default function Output({ isEditor = false, ...props}) {
  const useBlockProps = isEditor ? useBlockDefaultProps : useBlockDefaultProps.save;

  const {uniqueId, activetorClass, step, stepCmpltText, filterBtnPosition, enableCategoryFilter} = props.attributes;
  const aab_premium = aagb_local_object.licensing;
  const activetorClassEvent = aab_premium ? activetorClass : 'click';

  return (
    <>
      <CustomCSSBlock {...props} />
      <Other.Styles {...props} />

      <div
        {...useBlockProps({
          className: `searchable aagb_accordion_${uniqueId} ${activetorClassEvent} ${enableCategoryFilter && filterBtnPosition}`,
          id: `group-accordion-${uniqueId}`
        })}
      >
        <CategoryFilter {...props} />
        <Search {...props} />
        <ShowAllBtn {...props} />

        {
          isEditor ? (
            <InnerBlocks
              allowedBlocks={['aab/accordion-item']}
              template={[['aab/accordion-item']]}
            />
          ) : <InnerBlocks.Content/>
        }

        <ShowMoreBtn {...props} />
        
        {step && (
            <span className="step-result">{stepCmpltText}</span>
        )}
      </div>
      <Other.Scripts {...props} />
    </>
  );
};