import React from "react";
import { PanelBody, ToggleControl, BaseControl, SelectControl } from "@wordpress/components";
import {__} from '@wordpress/i18n';

const openALLBtnPositions = [
  {
    label: 'Left',
    value: 'left_btn',
  },
  {
    label: 'Center',
    value: 'center_btn',
  },
  {
    label: 'Right',
    value: 'right_btn',
  }
];

export default function ShowAllBtnPanel({ attributes, setAttributes }) {
  const { showAllbtn, openText, closeText, openALLBtnPosition } = attributes;
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';

  return (
    <PanelBody
      title={__(
        'Open/Close All',
        'advanced-accordion-block'
      )}
      initialOpen={false}
      className={has_disabled_class}
    >
      <ToggleControl
        label={__(
          'Open All & Close All',
          'advanced-accordion-block'
        )}
        disabled={!aab_premium}
        checked={aab_premium && showAllbtn}
        onChange={(showAllbtn) => setAttributes({ showAllbtn })}
      />
      {showAllbtn && aab_premium && (
        <>
          <BaseControl __nextHasNoMarginBottom id="openText" label="Show All Text">
            <input
              id="openText"
              type="text"
              disabled={!aab_premium}
              value={openText}
              onChange={(e) =>
                setAttributes({openText: e.target.value})
              }
              style={{
                display: 'block',
                width: '100%'
              }}
            />
          </BaseControl>
          <BaseControl __nextHasNoMarginBottom id="closeText" label="Close All Text">
            <input
              id="closeText"
              type="text"
              disabled={!aab_premium}
              placeholder={__(
                'Enter text',
                'advanced-accordion-block'
              )}
              value={closeText}
              onChange={(e) =>
                setAttributes({closeText: e.target.value})
              }
              style={{
                display: 'block',
                width: '100%'
              }}
            />
          </BaseControl>
          <SelectControl
            label={__(
              'Open/Close Button Position',
              'advanced-accordion-block'
            )}
            disabled={!aab_premium}
            options={openALLBtnPositions}
            onChange={(openALLBtnPosition) => setAttributes({ openALLBtnPosition })}
            value={openALLBtnPosition}
          />
        </>
      )}
    </PanelBody>
  )
}