import React from "react";

export default function ShowMoreBtn(props) {
  const {
    showMoreBtn,
    itemsToShow,
    showMoreBtnTxt,
    showLessBtnTxt,
    showMoreBtnColor,
    showMoreBtnBg,
    accessibilityOn
  } = props.attributes;
  
  const aab_premium = aagb_local_object.licensing;

  if(!aab_premium || !showMoreBtn) return <></>;
  
  return (
    <>
      <style>
        { accessibilityOn ?
          `
            .aab-show-more-btn-container button.show-more-btn:focus-visible, 
            .aab-show-more-btn-container button.show-less-btn:focus-visible {
              outline-color: ${showMoreBtnColor}33;
            }
          ` : ``
        }
      </style>
      <div className="aab-show-more-btn-container">
        <button
          tabIndex={accessibilityOn ? undefined : -1}
          className="show-more-btn"
          style={
            {
              color: showMoreBtnColor,
              backgroundColor: showMoreBtnBg
            }
          }
          data-items-to-show={itemsToShow}
        >
          {showMoreBtnTxt || "Show More"}
          <span className="aagb__icon dashicons dashicons-arrow-down"></span>
        </button>
        <button
          tabIndex={accessibilityOn ? undefined : -1}
          className="show-less-btn"
          style={
            {
              color: showMoreBtnColor,
              backgroundColor: showMoreBtnBg
            }
          }
        >
          {showLessBtnTxt || "Show Less"}
          <span className="aagb__icon dashicons dashicons-arrow-up"></span>
        </button>
      </div>
    </>
  );
}