import React from "react";
import { __ } from "@wordpress/i18n";
import { PanelBody, ToggleControl } from "@wordpress/components";
import AabColorPicker from "../../aab-colorpicker";

export default function AccessibilityPanel({ attributes, setAttributes }) {
  const { accessibilityOn, focusOutlineColor } = attributes;

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accessibility', 'advanced-accordion-block')}
    >
        <ToggleControl
            label={__(`Accessibility ${accessibilityOn ? "ON" : "OFF"}`, 'advanced-accordion-block')}
            checked={accessibilityOn}
            onChange={(accessibilityOn) => setAttributes({ accessibilityOn })}
        />
        {
          accessibilityOn ? (
            <AabColorPicker
              label={"Focused Accordion Color"}
              innerLabel={["Outline"]}
              value={focusOutlineColor}
              onChange={focusOutlineColor => setAttributes({ focusOutlineColor })}
              bgValue={undefined}
              onBgChange={undefined}
            />
          ) : null
        }
    </PanelBody>
  );
}