import React from "react";

export default function CustomCSSBlock(props) {
  const { uniqueId, customCSS } = props.attributes;

  if(!customCSS) return <></>;

  return (
    <div className="custom-css-block">
      <style>{`.aagb_accordion_${uniqueId} { ${customCSS} }`}</style>
    </div>
  );
}