import React from "react";
import {
  PanelBody,
  RangeControl,
  __experimentalBoxControl as BoxControl,
  __experimentalBorderBoxControl as BorderBoxControl,
  __experimentalToolsPanel as ToolsPanel,
  __experimentalToolsPanelItem as ToolsPanelItem
} from '@wordpress/components';
import {__} from '@wordpress/i18n';
import colors from "../../../colors";

export default function AccordionStylesPanel({ attributes, setAttributes }) {
  const { border, margins, paddings, borderRadius } = attributes;

  const onChangeBorder = (newBorders) => {
    setAttributes({border: newBorders});
    setAttributes({
      bodyBorder: {
        top: {
          color: `${newBorders.color}`,
          style: `${newBorders.style}`,
          width: `${newBorders.width}`
        },
      }
    })
  };

  return (
    <PanelBody
      initialOpen={false}
      title={__('Accordion Styles', 'advanced-accordion-block')}
    >
      <ToolsPanel
        label={__('Spacings', 'advanced-accordion-block')}
        className='aab-component-toolpanel'
        resetAll={() => {
          setAttributes({
            margins: {
              top: "",
              right: "",
              left: "",
              bottom: ""
            },
            paddings: {
              top: "",
              right: "",
              left: "",
              bottom: ""
            },
          });
        }}
      >
        <ToolsPanelItem
          hasValue={() => margins?.top || margins?.bottom}
          label={__('Margins', 'advanced-accordion-block')}
          onDeselect={() => setAttributes({
            margins: {
              top: "",
              right: "",
              left: "",
              bottom: ""
            }
          })}
        >
          <BoxControl
            values={margins}
            label={__(
              'Accordion Margin',
              'advanced-accordion-block'
            )}
            sides={['top', 'bottom']}
            units={[]}
            allowReset={false}
            onChange={(newValue) =>
              setAttributes({
                ...margins,
                margins: {
                  top: newValue.top,
                  bottom: newValue.bottom,
                },
              })
            }
          />
        </ToolsPanelItem>
        <ToolsPanelItem
          hasValue={() => paddings?.top || paddings?.right}
          label={__('Padding', 'advanced-accordion-block')}
          onDeselect={() => setAttributes({
            paddings: {
              top: "",
              right: "",
              left: "",
              bottom: ""
            }
          })}
        >
          <BoxControl
            values={paddings}
            label={__(
              'Content Padding',
              'advanced-accordion-block'
            )}
            units={[]}
            splitOnAxis
            allowReset={false}
            onChange={(newValue) =>
              setAttributes({
                ...paddings,
                paddings: {
                  top: newValue.top,
                  left: newValue.left,
                  right: newValue.right,
                  bottom: newValue.bottom,
                },
              })
            }
          />
        </ToolsPanelItem>
      </ToolsPanel>
      <ToolsPanel
        label={__('Border', 'advanced-accordion-block')}
        className='aab-component-toolpanel'
        resetAll={() => {
          onChangeBorder({
            "style": "none",
            "width": "0px"
          });
          setAttributes({borderRadius: 0});
        }}
      >
        <ToolsPanelItem
          hasValue={() => border?.width !== "0px" && !!border?.width}
          label={__('Border', 'advanced-accordion-block')}
          onDeselect={() => onChangeBorder({
            "style": "none",
            "width": "0px"
          })}
        >
          <BorderBoxControl
            colors={colors}
            onChange={onChangeBorder}
            value={border}
          />
        </ToolsPanelItem>
        <ToolsPanelItem
          hasValue={() => !!borderRadius}
          label={__('Border Radius', 'advanced-accordion-block')}
          onDeselect={() => setAttributes({borderRadius: 0})}
        >
          <RangeControl
            label={__('Border Radius', 'advanced-accordion-block')}
            value={borderRadius}
            onChange={(borderRadius) => setAttributes({borderRadius})}
            min={0}
            max={50}
          />
        </ToolsPanelItem>
      </ToolsPanel>
    </PanelBody>
  );
}