import React from "react";
import { PanelBody, __experimentalBorderBoxControl as BorderBoxControl, } from '@wordpress/components';
import {__} from '@wordpress/i18n';
import AabColorPicker from "../../../components/aab-colorpicker";
import colors from "../../../colors";

export default function AccordionBodyStylesPanel({ attributes, setAttributes }) {
  const { bodyBg, QaStyle, bodyBorder } = attributes;

  return (
    <PanelBody
      title={__('Accordion Body', 'advanced-accordion-block')}
      initialOpen={false}
    >
      <AabColorPicker
        label={__('Background Color', 'advanced-accordion-block')}
        value={bodyBg}
        onChange={(bodyBg) => setAttributes({bodyBg})}
        innerLabel={['Background']}
        bgValue={undefined}
        onBgChange={undefined}
      />
      {!QaStyle && (
        <>
          <p className="aagb__label">
            {__('Set Body Border', 'advanced-accordion-block')}
          </p>
          <BorderBoxControl
            colors={colors}
            label={__('Borders')}
            onChange={bodyBorder => setAttributes({ bodyBorder })}
            value={bodyBorder}
          />
        </>
      )}
    </PanelBody>
  );
}