import React from "react";
import { PanelBody, __experimentalBorderControl as BorderControl } from '@wordpress/components';
import {__} from '@wordpress/i18n';
import colors from "../../../colors";
import AabColorPicker from "../../../components/aab-colorpicker";

export default function ActiveAccordionStylesPanel({ attributes, setAttributes }) {
  const { activeAccordionBorder, activeBackground } = attributes;

  return (
    <PanelBody
      initialOpen={false}
      title={__(
        'Active Accordion Style',
        'advanced-accordion-block'
      )}
    >
      <p className="aab__label">
        {__('Border', 'advanced-accordion-block')}
      </p>
      <BorderControl
        colors={colors}
        value={activeAccordionBorder}
        onChange={(activeAccordionBorder) =>
          setAttributes({ activeAccordionBorder })
        }
        withSlider={true}
      />
      <p className="aab__sublabel">
        {__('(This style will only be visible in the frontend only)', 'advanced-accordion-block')}
      </p>
      <AabColorPicker
        label={__('Active Background', 'advanced-accordion-block')}
        value={activeBackground}
        onChange={(activeBackground) => setAttributes({ activeBackground })}
        bgValue={undefined}
        onBgChange={undefined}
      />
    </PanelBody>
  );
}