import React from "react";
import { PanelBody, ToggleControl, RangeControl, TextControl } from "@wordpress/components";
import {__} from '@wordpress/i18n';

export default function ReadMoreBtnPanel({ attributes, setAttributes }) {
  const { buttonShow, contentCount, readText } = attributes;
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';

  return (
    <PanelBody
      title={__('Read More Button', 'advanced-accordion-block')}
      initialOpen={false}
      className={has_disabled_class}
    >
      <ToggleControl
        label={__(
          'Button Show/Hide',
          'advanced-accordion-block'
        )}
        disabled={!aab_premium}
        checked={aab_premium && buttonShow}
        onChange={(buttonShow) => setAttributes({ buttonShow })}
      />
      {buttonShow && (
        <>
          <RangeControl
            label={__(
              'Content Count',
              'advanced-accordion-block'
            )}
            help={__('Total Number of Characters you want to display on accordion body', 'advanced-accordion-block')}
            disabled={!aab_premium}
            value={contentCount}
            onChange={(contentCount) => setAttributes({ contentCount })}
            min={1}
            max={1000}
          />
          <TextControl
            label={__('Read More Text', 'advanced-accordion-block')}
            value={readText}
            onChange={(readText) => setAttributes({ readText })}
            help={__('Enter the text you want to display on Read more button.', 'advanced-accordion-block')}
          />
        </>
      )}
    </PanelBody>
  )
}