import React from "react";
import { PanelBody, ToggleControl } from "@wordpress/components";
import {__} from '@wordpress/i18n';

export default function AccordionSearchPanel({ attributes, setAttributes }) {
  const { searchShow, placeholderText } = attributes;
  const aab_premium = aagb_local_object.licensing;
  const has_disabled_class = aab_premium ? '' : 'aab-pro-element';

  return (
    <PanelBody
      title={__('Accordion Search', 'advanced-accordion-block')}
      initialOpen={false}
      className={has_disabled_class}
    >
      <ToggleControl
        label={__(
          'Show Accordion Search',
          'advanced-accordion-block'
        )}
        disabled={!aab_premium}
        checked={aab_premium && searchShow}
        onChange={(searchShow) =>
          setAttributes({ searchShow })
        }
      />
      {searchShow && (
        <div>
          <label
            className="label-d-block"
          >
            {__(
              'Placeholder Text',
              'advanced-accordion-block'
            )}
          </label>
          <input
            type="text"
            className="widefat"
            disabled={!aab_premium}
            placeholder={__(
              'Enter placeholder text',
              'advanced-accordion-block'
            )}
            value={placeholderText}
            onChange={(e) =>
              setAttributes({
                placeholderText: e.target.value,
              })
            }
          />
        </div>
      )}
    </PanelBody>
  )
}